</template>

<script type="text/ecmascript-6">
	import db from "@/common/js/db.js";

	export default {
		name: 'intro',
		data() {
			return {
				hobbies: {},
				education: {},
				workExperience: {},
				skills: {},
				projects: {},
				selfEvaluation: {},
				showAll: false,
				isResume: true
			}
		},
		computed: {
			visibleProjects() {
				return this.showAll ? this.projects.content : this.projects.content.slice(0, 3);
			},
			hiddenProjectsCount() {
				return this.projects.content.length - 3;
			}
		},

		created() {
			const dbData = db();
			const infos = dbData.info;
			this.isResume = dbData.type === 'resume'
			let hobbyTit = this.isResume ? '求职意向' : '个人爱好'
			let eduTit = this.isResume ? '教育背景' : 'Logo拆解'

			// Extract all sections
			this.hobbies = infos.find(item => item.tit === hobbyTit) || {};
			this.education = infos.find(item => item.tit === eduTit) || {};
			this.workExperience = infos.find(item => item.tit === '工作经历') || {};
			this.skills = infos.find(item => item.tit === '职业技能') || {};
			this.projects = infos.find(item => item.tit === 'UI设计/前端主要项目') || {};
			let selfEvaluation = infos.find(item => item.tit === '自我评价') || {};
			this.selfEvaluation = selfEvaluation.content[0][0]
		},
		mounted() {
			// 默认页面水平滚动到右边（适配手机端）
			window.scrollTo({
			  left: document.body.scrollWidth,
			  behavior: 'auto'
			});
		},
		methods: {
			getSkillLevel(score) {
				if (score > 85) return '精通';
				if (score >= 70) return '熟练';
				return '掌握';
			},
			showAllProjects() {
				this.showAll = !this.showAll;
			},
			toTop(){
				// 默认页面水平滚动到右边（适配手机端）
				window.scrollTo({
				  top: 0,
				  behavior: 'smooth' // 可选，平滑滚动
				});
			}
		},
	}
</script>

