			}
		}
		.intro_skills_block{
			width: 30%;
			margin-right: 4%;
			margin-top: 25px;

			&:nth-child(4n){
				margin-right: 4%;
			}
			&:nth-child(3n){
				margin-right: 0;
			}
		}
		.intro_skills{
			padding: 30px;
		}
		.intro_items{
			overflow: hidden;
		}
		.word_video{
			max-height: 350px;
		}
    .intro_bottom{
      margin-top: 150px;
    }
    .intro_title_3{
      margin-bottom: 30px;
    }
    .intro_skills{
      margin: 40px 0;
      padding-bottom: 40px;
    }
    .intro_title_2{
      margin-bottom: 0;
    }
    .intro_job_body{
      flex-wrap: wrap;
    }
    .intro_job_block{
      align-items: start;
      width: 45%;

      &:last-child{
        width: 100%;
        margin-top: 40px;
      }
    }
	.intro_img_4{
		width: 95%;
	}
	}
</style>



